// mixin for dekstop display
@mixin for-desktop() {
    @media only screen and (min-width: 996px) {
        @content;
    }
}

@mixin for-very-small() {
    @media only screen and (min-width: 600px) {
        @content;
    }
}

@mixin boxShadow($x, $y) {
    box-shadow: $x $y 15px 1px hsl(210, 26%, 13%)
}